<template>
  <v-skeleton-loader
    :loading="loading"
    type="article, list-item-two-line, actions"
  >
    <v-tabs centered>
      <v-tab>{{ $t("restaurant") }}</v-tab>
      <v-tab>{{ $t("account") }}</v-tab>

      <v-tab-item>
        <v-card flat>
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                {{ $t("onlineOrdering") }}
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveOnlineOrdering()"
                  >{{ $t("save") }}</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12">
                  <v-switch
                    class="mt-0 pt-0"
                    v-model="restaurant.isOpen"
                    :label="
                      restaurant.isOpen
                        ? $t('orderingEnabled')
                        : $t('orderingDisabled')
                    "
                    color="primary"
                    hide-details
                  ></v-switch>
                </v-col>
                <v-col cols="12" md="4">
                  <v-menu
                    ref="menu"
                    v-model="timePickerMenuStatus1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="time"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="restaurant.dailyMenuOrderingTime.from"
                        :label="$t('dailyMenuOrderingStart')"
                        append-icon="mdi-clock-time-four-outline"
                        readonly
                        filled
                        rounded
                        hide-details
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      v-if="timePickerMenuStatus1"
                      full-width
                      @click:minute="$refs.menu.save(time)"
                      v-model="restaurant.dailyMenuOrderingTime.from"
                      format="24hr"
                    ></v-time-picker>
                  </v-menu>
                </v-col>
                <v-col cols="12" md="4">
                  <v-menu
                    ref="menu"
                    v-model="timePickerMenuStatus2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="time"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="restaurant.dailyMenuOrderingTime.to"
                        :label="$t('dailyMenuOrderingEnd')"
                        append-icon="mdi-clock-time-four-outline"
                        readonly
                        filled
                        rounded
                        hide-details
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      v-if="timePickerMenuStatus2"
                      full-width
                      @click:minute="$refs.menu.save(time)"
                      v-model="restaurant.dailyMenuOrderingTime.to"
                      format="24hr"
