                </v-col>
                <!-- <v-col cols="6" md="2">
                  <v-text-field
                    v-model="restaurant.deliveryOptions.delivery.price"
                    label="Delivery price"
                    type="number"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col> -->
                <v-col cols="auto">
                  <span class="mt-0 pt-0 mr-4 d-inline float-left"
