<script>
import settingsService from "../services/settings.service";

export default {
  data() {
    return {
      test_model: null,
      loading: true,
      dialog: false,
      timePickerMenuStatus1: false,
      timePickerMenuStatus2: false,
      time: [
        "0:00",
        "0:30",
        "1:00",
        "1:30",
        "2:00",
        "2:30",
        "3:00",
        "3:30",
        "4:00",
        "4:30",
        "5:00",
        "5:30",
        "6:00",
        "6:30",
        "7:00",
        "7:30",
        "8:00",
        "8:30",
        "9:00",
        "9:30",
        "10:00",
