      this.loading = true;
      const info = this.restaurant.info;
      const resp = await settingsService.editRestaurantInfo(
        localStorage.getItem("restaurantId"),
        info
      );
      if (resp) {
        this.restaurant.info = resp;
        this.loading = false;
      }
    },
    async saveDefaultMessages() {
      this.loading = true;
      const defaultMessages = this.restaurant.defaultMessages;
      const resp = await settingsService.editDefaultMessages(
        localStorage.getItem("restaurantId"),
        defaultMessages
      );
      if (resp) {
        this.restaurant.defaultMessages = resp;
        this.loading = false;
      }
    },
    async saveCustomHeadings() {
      this.loading = true;
      const customHeadings = this.restaurant.customHeadings;
      const resp = await settingsService.editCustomHeadings(
        localStorage.getItem("restaurantId"),
        customHeadings
      );
      if (resp) {
        this.restaurant.customHeadings = resp;
        this.loading = false;
      }
